.App,
html {
  background: #f1fae8;
}

.App {
  text-align: center;
}

nav ul button:hover {
  color: #9f2718;
}

main button:hover {
  background: #ffd134;
  color: black;
}


a {
  text-decoration: none;
}

footer a {
  color: #9f2718;
}

footer a:hover {
  text-decoration: underline;
}

ul a,
.drop-down li {
  color: #f1fae8;
}

ul a:hover {
  color: #ffd134;
}

h1,
h2,
h3,
h4,
li,
p,
ul {
  margin: 0;
}

h1, 
button {
  font-family: 'Josefin Sans', sans-serif;
}

h1 {
  font-size: 5rem;
  position: relative;
  color: #f1fae8;
}

body {
  font-family: 'Lato', sans-serif;
  letter-spacing: 0.8px;
  font-size: 125%;
}

header {
  width: 100%;
  margin: 0 auto;
  position: relative;
  background-image: linear-gradient(10deg, rgba(0,0,0,0), rgb(50, 10, 0)), url(./../assets/header-image.jpg);
  opacity: 0.9;
  background-size: cover;
  height: 100vh;
  z-index: 999;
}

.title-info-container,
.title-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.title-container {
  width: 400px;
  height: 400px;
  text-align: left;
  border: 30px solid #ffd134;
  padding: 40px;
  position: absolute;
  top: -5vh;
  left: 10vw;
  border-radius: 50%;
  background-color: rgba(100, 0, 0, 0.6);
}

nav {
  background: #ffd134;
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

nav ul.menu {
  width: 400px;
  display: flex;
  justify-content: space-around;
}

ul button {
  background: none;
  border: none;
  font-size: 1.3rem;
  position: relative;
}

ul.saved-items {
  height: 300px;
  top: 60px;
  right: 60px;
  max-height: 200px;
}

ul.compared-items {
  height: auto;
  top: 60px;
  right: 270px;
  padding-bottom: 20px;
}

ul.compared-items li:last-of-type{
  background: #FAFAFA;
  margin-top: 10px;
  
}

.btn {
  background: none;
  width: 100%;
  padding: 0 10px 0;
  margin: 0;
  color: black;
}

.compare-btn2 {
  padding: 0;
  margin: 0 auto;
  color: black;
}

ul.drop-down {
  text-align: left;
  position: absolute;
  background: #9f2718;
  color: #FAFAFA;
  width: 150px;
  padding: 15px;
  height: auto;
  overflow: auto;
}

ul.drop-down li,
.menu li {
  border-bottom: 0;
}

ul.drop-down li {
  padding: 15px;
}


nav {
  display: flex;
  justify-content: space-between;
  padding: 15px 60px;
}

.gallery-field {
  display: flex;
  justify-content: center;
  padding-bottom: 50px;
}

.item-card img {
  object-fit: cover;
  margin-bottom: 20px;
  max-height: 79%;
  width: 50%;
}

.item-card {
  height: 400px;
  width: 100vw;
  max-width: 400px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  padding: 10px;
  margin-left: 30px;
  margin-right: 30px;
  margin-bottom: 100px;
  position: relative;
}

.item-card .save-item-btn {
  position: absolute;
  bottom: -80px;
  left: 0;
  padding: 15px;
}

.item-card .compare-btn {
  position: absolute;
  bottom: -80px;
  right: 0;
  padding: 15px;
}

form label,
form input {
  text-align: center;
}

form label {
  margin: 20px auto;
  font-size: 2rem;
}

form input {
  margin-bottom: 20px;
}

.item-card button,
.buttons button {
  border: none;
  background: #9f2718;
  color: #FAFAFA;
  font-size: 1.2rem;
}

.buttons button {
  transition: 0.2s ease;
}

.buttons button:hover:focus {
  background-color: #f1fae8;
  border: 1px solid #9f2718;
  color: #9f2718;
}

.buttons button:active {
  color: #9f2718;
}

.item-card button,
form button,
form input {
  padding: 15px 30px;
}

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 100px 0;
}

form {
  display: flex;
  width: 60%;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  padding: 60px 0;
}

form .buttons {
  display: flex;
  justify-content: space-between;
}

form input {
  width: 90%;
}

.nutrition-card {
  width: 100%;
  text-align: left;
  border: 2px solid black;
  background: #FAFAFA;
  align-self: center;
  margin: 0 auto;
  overflow-y: scroll;
  font-size: 1.1rem;
  padding: 7px;

}

.nutrition-card h2 {
  text-transform: capitalize;
  font-size: 1.3rem;
  margin-bottom: 5px;
}

.nutrition-card p {
  padding-bottom: 3px;
  
}

.nutrition-card p:nth-of-type(even) {
  display: flex;
  width: 40%;
  justify-content: flex-end;
  align-content: flex-end;
}

.show-selected .card-info {
  padding: 20px;
}

.nutrition-card .line {
  border-bottom: 6px solid black;
}

.show-selected .card-info .nutrition-card {
  margin-left: auto;
  margin-right: 20px;
  width: 93%;
}

.compare-container .nutrition-card {
  margin: 10px;
}

li {
  list-style: none;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid black;
}

li

.item-card li:not(:last-of-type) {
  border-bottom: 2px solid black;
}

ul {
  padding: 0;
  margin: 0;
  
}

footer {
  padding: 20px 0;
  background: #ffd134;
}

.wrapper {
  width: 90%;
  margin: 0 auto;
  max-width: 1600px;
}

.compare-container {
  width: 800px;
  display: flex;
  justify-content: space-around;
  margin: 0 auto;
  margin-bottom: 60px;
}

.compare-container div {
  width: 40%;
}

.compare-container .nutrition-card {
  width: 100%;
}


/* MEDIA QUERIES */

@media (max-width: 1159px) {
    .gallery-field .card-info {
      padding: 0;
    }

    .title-info-container {
      left: 50vw;
    }
}

@media (max-width: 1106px) {
    .gallery-field .inner-card,
    .wrapper {
      width: 95%;
    }
}

@media (max-width: 936px) {
    .wrapper,
    .gallery-field .inner-card {
      width: 100%;
    }
}

@media (max-width: 821px) {
    .compare-container {
      width: 700px;
    }

  .item-card {
      height: 100%;
      width: 100%;
      max-width: 500px;
    }
    
  }

@media (max-width: 765px) {
    .wrapper, .gallery-field .inner-card {
      width: 95%;
    }

    .title-info-container, .title-container {
      justify-content: flex-start;
    }

    h1 {
      font-size: 4rem;
      text-align: center;
    }
    .compare-container {
      flex-direction: column;
      align-items: center;
    }
    .compare-container div {
      width: 80%;
    }
  }

@media (max-width: 722px) {
    .compare-container {
      width: 100%;
    }
  }

@media (max-width: 667px) {
    .item-card .compare-btn {
      right: 0;
    }

    .item-card {
      width: 400px;
    }

    .title-container {
      left: 0;
    }
}

@media (max-width: 620px) {
    .lines {
      position: static;
    }

    h1 {
      top: 60px;
    }

    .compare-container {
      display: block;
    }
}


@media only screen and (max-width: 557px) {
    .title-container {
      padding-top: 60px;
      margin: 0 auto;
      top: 0;
      border-radius: 0;
      width: auto;
    }

    h1 {
      font-size: 3rem;
    }

    .item-card {
      width: 350px;
    }

    .item-card .save-item-btn,
    .item-card .compare-btn
    {
      position: static;
    }

    .compare-container {
      display: block;
    }

    .item-card .save-item-btn {
      margin-bottom: 10px;
    }
}

@media only screen and (max-width: 475px) {
    .title-container {
      width: auto;
    }

    .item-card {
      margin: 0 auto;
    }
    
    header {
      background-position: center;
    }
    
    ul.compared-items {
      right: 0;
    }
}

@media only screen and (max-width: 430px) {
    h1 {
      font-size: 2.5rem;
    }

    h4 {
      display: none;
    }

    .item-card .save-item-btn {
      position: static;
    }

    .wrapper, .gallery-field .inner-card {
      width: 100%;
    }

    form label {
      font-size: 1.5rem;
    }

    form {
      flex-direction: initial;
    }
  
}

@media only screen and (max-width: 337px) {
  h1 {
    font-size: 2rem;
  }
}

